import React from 'react';
import {View, Text} from 'react-native';
import EStyleSheet from 'react-native-extended-stylesheet';

export default class Header extends React.Component {
  render () {
    return (
      //Header
      (
        <View style={{height: '100vh', backgroundColor: '#ECECEC'}}>
          <View style={styles.header}>
            <View style={styles.headerLeft}>
              <Text style={styles.nav}>PREVIOUS WORK</Text>
              <Text style={styles.nav}>PROJECTS</Text>
            </View>
            <View style={styles.headerCenter}>
              <Text style={styles.name}>GABRIEL GRUTER</Text>
            </View>
            <View style={styles.headerRight}>
              <Text style={styles.nav}>CV</Text>
              <Text style={styles.nav}>CONTACT ME</Text>
            </View>
          </View>
        </View>
      )
      //End of Header
    );
  }
}

const styles = EStyleSheet.create ({
  header: {
    height: '3vw',
    flexDirection: 'row',
    justifyContent: 'center',
  },
  headerLeft: {
    width: '25%',
    flexDirection: 'row',
    alignItems: 'center',
  },
  headerCenter: {
    width: '50%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    paddingTop: '4.5vw',
  },
  headerRight: {
    width: '25%',
    justifyContent: 'flex-end',
    alignItems: 'center',
    flexDirection: 'row',
  },
  nav: {
    fontFamily: 'Roboto',
    marginLeft: '10%',
    marginRight: '10%',
    fontSize: '1vw',
  },
  name: {
    fontFamily: 'Playfair Display SC',
    fontSize: '3.5vw',
  },
});
